/*----- Accordion -----*/
.accordion, .accordion * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.accordion {
  position: relative;
  z-index: 2;
  padding-bottom: 50px;
  overflow: hidden;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  background: #f7f7f7; }
  .accordion__wrapper {
    position: relative; }
  .accordion__nav {
    margin: 0;
    padding: 0;
    text-align: center;
    list-style: none; }
  .accordion__nav-item {
    position: relative;
    border-top: 1px dashed transparent;
    border-bottom: 1px dashed #000;
    background-color: #fee300;
    transition: all 0.5s; }
    @media (min-width: 1200px) {
      .accordion__nav-item:hover {
        border-top: 1px dashed #000;
        transform: translateY(-10px); }
      .accordion__nav-item:before {
        position: absolute;
        z-index: -1;
        content: '';
        left: calc(50% - 10px);
        bottom: 0;
        border-style: solid;
        border-width: 10px 10px 0 10px;
        border-color: #000 transparent transparent transparent;
        transition-duration: 0.3s;
        transition-property: transform;
        transform: translateY(10px); }
      .accordion__nav-item:last-child:before {
        border: none; } }
  .accordion__nav-link {
    display: block;
    padding: 12px;
    text-decoration: none;
    font-size: 16px;
    font-weight: 600;
    color: #000; }
  .accordion__content {
    padding-right: 20px;
    padding-left: 20px; }
    @media (min-width: 1200px) {
      .accordion__content {
        padding-right: 50px;
        padding-left: 50px; } }
  .accordion__link {
    display: inline-block;
    margin-top: 50px;
    margin-bottom: 15px;
    font-size: 20px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    color: #666; }
    @media (min-width: 1200px) {
      .accordion__link {
        font-size: 25px; } }

